import React from 'react';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';
import * as Table from './Tables';

const Modal = () => { 
	const title = 'Modal';
	const context = `it is a dialog box that will show your content in the top of your page.`;

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`${title} - Neumorphism Project | Mytabworks`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer 
				title={title}
				context={context}
				main={<ModalMain />}
				side={<ModalSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Modal.propTypes = {};

const ModalMain = () => (
	<>
		<Section.Example />  
        <Section.LiveDemo />
		<Section.StaticBackdrop />
		<Section.Sizes />
		<Section.Javascript />
		<Table.Methods />
		<Table.Events />
		<Section.Events />
	</>
);

const ModalSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Modal Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.modal}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius']}
			allowFloat
		/>
	</ul>
);

export default Modal;