import React, { useEffect } from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
import N from '../../scripts';

export const Example = () => (
	<Section
		title='Example'
		context='you can choose the modal shadow style by float or flat.'>
		<HoverCopy code={code.Example}>
			<CodeReadOnly code={code.Example} />

			<div
				className='n-modal show for-show'
				data-backdrop='static'
				tabIndex='-1'
				role='dialog'
				style={{ display: 'block', position: 'static', overflow: 'unset' }}>
				<div className='n-modal-dialog'>
					<div className='n-modal-content'>
						<div className='n-modal-header'>
							<h5 className='n-modal-title'>Modal Title</h5>
							<button className='n-btn n-btn-xs'>&times;</button>
						</div>
						<div className='n-modal-body'>Modal body hire</div>
						<div className='n-modal-footer'>
							<button className='n-btn'>Close</button>
						</div>
					</div>
				</div>
			</div>
		</HoverCopy>
	</Section>
);

export const LiveDemo = () => {
	useEffect(() => {
		const element = document.querySelector('#live-demo .n-modal');
		const instance = N.modal.getInstance(element);
		return () => instance.destroy();
	}, []);

	return (
		<Section
			title='Live Demo'
			context='you can click the button below to see the live demo.'>
			<HoverCopy code={code.LiveDemo}>
				<CodeReadOnly code={code.LiveDemo} />
				<button className='n-btn' data-modal='#modal-2'>
					Button with data-modal
				</button>
				<a className='n-btn ml-2' href='#modal-2' role='button'>
					Anchor with href
				</a>
				<div id='modal-2' className='n-modal' tabIndex='-1' role='dialog'>
					<div className='n-modal-dialog'>
						<div className='n-modal-content'>
							<div className='n-modal-header'>
								<h5 className='n-modal-title'>Modal Title</h5>
								<button className='n-btn n-btn-xs' data-dismiss='modal'>
									&times;
								</button>
							</div>
							<div className='n-modal-body'>
								<p>
									Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
									do eiusmod tempor incididunt ut labore et dolore magna aliqua.
									Ut enim ad minim veniam, quis nostrud exercitation ullamco
									laboris nisi ut aliquip ex ea commodo consequat. Duis aute
									irure dolor in reprehenderit in voluptate velit esse cillum
									dolore eu fugiat nulla pariatur. Excepteur sint occaecat
									cupidatat non proident, sunt in culpa qui officia deserunt
									mollit anim id est laborum.
								</p>
							</div>
							<div className='n-modal-footer'>
								<button className='n-btn' data-dismiss='modal'>
									Close
								</button>
							</div>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const StaticBackdrop = () => {
	useEffect(() => {
		const element = document.querySelector('#static-backdrop .n-modal');
		const instance = N.modal.getInstance(element);
		return () => instance.destroy();
	}, []);

	return (
		<Section
			title='Static Backdrop'
			context={
				<>
					when you set <b>data-backdrop="static"</b> on .n-modal then it will
					not close even your click outside the modal.
				</>
			}>
			<HoverCopy code={code.StaticBackdrop}>
				<CodeReadOnly code={code.StaticBackdrop} />
				<button className='n-btn' data-modal='#modal-3'>
					Launch static backdrop modal
				</button>
				<div
					className='n-modal'
					id='modal-3'
					data-backdrop='static'
					tabIndex='-1'
					role='dialog'>
					<div className='n-modal-dialog'>
						<div className='n-modal-content'>
							<div className='n-modal-header'>
								<h5 className='n-modal-title'>Modal Title</h5>
								<button className='n-btn n-btn-xs' data-dismiss='modal'>
									&times;
								</button>
							</div>
							<div className='n-modal-body'>
								<div className='n-form-field'>
									<input
										className='n-form-control'
										type='text'
										placeholder='enter text...'
										name='text'
									/>
								</div>
								<div className='n-form-field'>
									<label className='n-form-check'>
										<input
											className='n-form-control'
											type='checkbox'
											name='cb'
											value='1'
										/>
										checkbox
									</label>
								</div>
								<div className='n-form-field'>
									<label className='n-form-check'>
										<input
											className='n-form-control'
											type='radio'
											name='r'
											value='1'
										/>
										radio
									</label>
								</div>
								<div className='n-form-field'>
									<input className='n-form-control' type='range' name='range' />
								</div>
								<div className='n-form-field'>
									<input className='n-form-control' type='color' name='color' />
								</div>
								<div className='n-form-field'>
									<textarea
										className='n-form-control'
										name='comment'
										placeholder='enter comment...'
									/>
								</div>
								<div className='n-form-field'>
									<select className='n-form-control'>
										<option value=''>choose wisely...</option>
										<option value='1'>option 1</option>
										<option value='2'>option 2</option>
									</select>
								</div>
							</div>
							<div className='n-modal-footer'>
								<button className='n-btn' data-dismiss='modal'>
									Close
								</button>
								<button className='n-btn' data-dismiss='modal'>
									Save
								</button>
							</div>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const Sizes = () => {
	useEffect(() => {
		const elements = Array.from(
			document.querySelectorAll('#optional-sizes .n-modal')
		);
		const instances = elements.map((element) => N.modal.getInstance(element));
		return () => instances.forEach((instance) => instance.destroy());
	}, []);

	return (
		<Section
			title='Optional sizes'
			context={
				<>
					modals have three optional sizes <b>.n-modal-sm</b>,{' '}
					<b>.n-modal-lg</b>, and <b>.n-modal-xl</b> which can be apply in{' '}
					<b>.n-modal-dialog</b> class.
				</>
			}>
			<HoverCopy code={code.Sizes}>
				<CodeReadOnly code={code.Sizes} />
				<button data-modal='#modal-sm' className='n-btn'>
					Small Modal
				</button>
				&nbsp;&nbsp;
				<button data-modal='#modal-lg' className='n-btn'>
					Large Modal
				</button>
				&nbsp;&nbsp;
				<button data-modal='#modal-xl' className='n-btn'>
					Extra Large Modal
				</button>
				&nbsp;&nbsp;
				<div id='modal-sm' className='n-modal' tabIndex='-1' role='dialog'>
					<div className='n-modal-dialog n-modal-sm'>
						<div className='n-modal-content'>
							<div className='n-modal-header'>
								<h5 className='n-modal-title'>Modal Title</h5>
								<button className='n-btn n-btn-xs' data-dismiss='modal'>
									&times;
								</button>
							</div>
							<div className='n-modal-body'>
								<p>Small modal.</p>
							</div>
							<div className='n-modal-footer'>
								<button className='n-btn' data-dismiss='modal'>
									Close
								</button>
							</div>
						</div>
					</div>
				</div>
				<div id='modal-lg' className='n-modal' tabIndex='-1' role='dialog'>
					<div className='n-modal-dialog n-modal-lg'>
						<div className='n-modal-content'>
							<div className='n-modal-header'>
								<h5 className='n-modal-title'>Modal Title</h5>
								<button className='n-btn n-btn-xs' data-dismiss='modal'>
									&times;
								</button>
							</div>
							<div className='n-modal-body'>
								<p>Large modal.</p>
							</div>
							<div className='n-modal-footer'>
								<button className='n-btn' data-dismiss='modal'>
									Close
								</button>
							</div>
						</div>
					</div>
				</div>
				<div id='modal-xl' className='n-modal' tabIndex='-1' role='dialog'>
					<div className='n-modal-dialog n-modal-xl'>
						<div className='n-modal-content'>
							<div className='n-modal-header'>
								<h5 className='n-modal-title'>Modal Title</h5>
								<button className='n-btn n-btn-xs' data-dismiss='modal'>
									&times;
								</button>
							</div>
							<div className='n-modal-body'>
								<p>Extra Large modal.</p>
							</div>
							<div className='n-modal-footer'>
								<button className='n-btn' data-dismiss='modal'>
									Close
								</button>
							</div>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const Javascript = () => (
	<Section
		title='Javascript'
		context={
			<>
				using <b>N.modal.getInstance(modalElement)</b> it can initialize the{' '}
				<b>.n-modal</b> when it is not initialize yet and get the instance at
				the same time. if the <b>.n-modal</b> is already initialize, it will
				only get the instance.
			</>
		}>
		<HoverCopy code={code.Javascript}>
			<CodeReadOnly code={code.Javascript} />
		</HoverCopy>
	</Section>
);

export const Events = () => (
	<Section>
		<HoverCopy code={code.Events}>
			<CodeReadOnly code={code.Events} />
		</HoverCopy>
	</Section>
);
