import React from 'react';
import { Section, Table } from 'Templates';

const MethodsRows = [
	{
		method: 'Instance.show',
		return: 'void', 
		description: 'to show the modal programmatically.',
	},
	{
		method: 'Instance.close',
		return: 'void', 
		description: 'to close the modal programmatically.', 
	},
	{
		method: 'Instance.destroy',
		return: 'void', 
		description: 'it is use to destroy and kill the modal instance.',
	},
]

const EventRows = [
    {
		event: 'show.n.modal', 
		description: 'This event fires immediately when the show instance method is called.',
    },
    {
		event: 'shown.n.modal', 
		description: 'This event fires when the modal is completely shown (CSS transition is complete).',
    },
    {
		event: 'hide.n.modal', 
		description: 'This event fires immediately when the close instance method is called.',
    },
    {
		event: 'hidden.n.modal', 
		description: 'This event fires when the modal is completely hidden (CSS transition is complete).',
    },
]

export const Methods = () => (
	<Section
		title='Methods'>
		<Table data={MethodsRows} />
	</Section>
);

export const Events = () => (
	<Section
		title='Events'
        context={<>All modal events are fired at the <b>.n-modal</b> element.</>}>
		<Table data={EventRows} />
	</Section>
);